<template>
  <div style="margin: 25px">
    <el-card style="height: 900px">
      <el-breadcrumb separator-class="el-icon-arrow-right" style="height: 40px">>
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>人事管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/' }">巡岗管理</el-breadcrumb-item>
        <el-breadcrumb-item>巡岗查询</el-breadcrumb-item>
      </el-breadcrumb>
      <el-form  :inline="true" size="small" :model="formline" label-width="90px">
        <el-form-item>
          <el-button type="primary" @click="search()"size="medium" >刷新</el-button>
        </el-form-item>

      </el-form>


      <template>
        <vue-event-calendar :events="demoEvents" >

        </vue-event-calendar>
      </template>




      <!--<vue-event-calendar :events="demoEvents">-->
        <!--<template slot-scope="props">-->
          <!---->
          <!--<li v-for="item in obj">{{item}}</li>-->
        <!--</template>-->
      <!--</vue-event-calendar>-->
      </el-card>


  </div>
</template>

<script>

  let today = new Date()
  export default {
    name: 'app',
    data () {
      return {
        demoEvents: [{
          date: '',
          title:'',
          desc: ''
        },],

        uid:'',


    }
    },

    methods: {

      search:function () {


        var user = sessionStorage.getItem('user');
        if (user) {
          user= JSON.parse(user);
        }

        var formData={
          uid: user.uid

        }
        var qs = require('querystring')
        var _self = this;
        this.$ajax.post('/cms-attendance-web/getPatrolById',
          qs.stringify(formData)).then(function(result){
          var list = result.data.list;
          for(var i=0;i<list.length;i++) {
           _self.demoEvents[0].date=list[i].patDate;
            _self.demoEvents[0].title=list[i].userName;
            _self.demoEvents[0].desc=list[i].addnessInfo;
          }



        })


        _self.demoEvents[0].title="巡岗";
        this.demoEvents[0].desc="路线地点";


      },


      handleDayChanged (data) {
        console.log('date-changed', data)
      },
      handleMonthChanged (data) {
        console.log('month-changed', data)
      }
    }



  }
</script>

<style>
  .el-checkbox+.el-checkbox {
    margin-right: 20px;
    margin-left: 0px;
  }
</style>




